* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,400&display=swap');
    font-family: 'Poppins', sans-serif; }

header {
    padding-top: 50px;
    padding-bottom: 50px;
    h1 {
        color: gray;
        font-size: 36px;
        font-weight: 400;
        text-align: center;
        strong {
            color: black; } } }

.empty {
			position: absolute;
			top: 20rem;
			right: 34rem;
			color: gray;
			font-size: 26px;
			strong {
				color: #000; } }
.lds-hourglass {
	display: inline-block {
  position: relative;
  width: 80px;
  height: 80px; }
	&:after {
		content: " ";
		display: block;
		border-radius: 50%;
		width: 0;
		height: 0;
		margin: 8px;
		box-sizing: border-box;
		border: 32px solid black;
		border-color: #9c2d2d transparent #391f6a transparent;
		animation: lds-hourglass 1.2s infinite; } }

@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }

  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

  100% {
    transform: rotate(1800deg); } }

.content-wrap {
	display: flex;

	aside {
		flex: 1 1 250px;
		max-width: 250px;
		padding-right: 16px;

		nav {
			padding-left: 16px;
			padding-right: 16px;

			h3 {
				text-align: center;
				color: #313131;
				font-size: 24px;
				margin-bottom: 16px; }


			a {
				display: block;
				padding: 8px 16px;
				text-align: left;

				color: #888;
				font-size: 16px;
				margin-bottom: 16px;

				background-color: #EEE;
				border-radius: 999px;
				text-decoration: none;
				cursor: pointer;
				transition: 0.4s;

				&:hover {
					background-color: #313131;
					color: #AAA; } } } }





	main {
		flex: 1 1 0%;
		padding: 0px 16px;

		.main-head {
			display: flex;
			justify-content: flex-end;



			.search-box {
				display: flex;
				justify-content: flex-end;
				flex: 1 1 0%;

				input {
					appearance: none;
					background: none;
					outline: none;
					border: none;

					display: block;
					width: 100%;
					max-width: 400px;
					padding: 16px;
					border-radius: 999px;
					background-color: #EEE;

					transition: 0.4s;

					::placeholder {
						color: #888; }


					&:focus, &:valid {
						background-color: #313131;
						color: #FFF; } } } }





		.anime-list {
			display: flex;
			flex-wrap: wrap;
			margin-top: 32px;
			margin: 32px -8px 0px;

			.anime-card {
				flex: 1 1 33.333%;
				max-width: 33.333%;
				padding: 16px 8px;

				a {
					text-decoration: none; }


				figure {
					display: block;
					margin-bottom: 16px;

					img {
						display: block;
						width: 70%;
						height: 350px;
						object-fit: cover;
						border-radius: 16px;
						box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.15);

						transition: 0.4s; } }



				h3 {
					color: #313131; }


				&:hover {
					figure {
						img {
							box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.25);
							transform: scale(1.05); } } } } } } }




@media (max-width: 500px) {
	aside {
		display: none; }
	.content-wrap {
		main {
			.anime-list {
				display: block;
				.anime-card {
					display: block;
					margin-left: 4rem;
					max-width: 100%; } } } } }




